@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap');

ul {
    list-style: none;
    padding-left: 0;
}

* {
    font-family: 'Rubik', sans-serif;
}


.invalid {
    input{
        border: 0.1rem solid red;
        background-color:#3f1d1da4;
        transition: 300ms;
        &:hover{
        border: 0.1rem solid red;
        background-color:#303245;
          }
    }
    p {
        color:rgb(218, 63, 63);
        margin-top:0;
        transition: 400ms;
    }
}

.site {
    &__description {
        text-align: center;
    }
}